body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }
  code .test {
    color: aqua; }

.link {
  text-decoration: none;
  font-weight: normal;
  font-style: normal; }

h1, h2, h3, h4, h5, h6 {
  color: #2D2D2D; }

.App {
  text-align: center; }
  .App .App-header {
    background-color: #F2F2F2;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white; }
    .App .App-header .App-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
      height: 40vmin; }

.App-link {
  color: #61dafb;
  margin: 30px auto; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.section .section_title {
  font-size: 2.25em;
  text-align: center;
  color: #2D2D2D;
  font-weight: 700; }

.section.section_one .section_title {
  letter-spacing: .1em; }

.section.section_one .enter_anchor {
  text-decoration: none;
  color: #FFFFFF; }
  .section.section_one .enter_anchor .enter_btn {
    font-size: .75em;
    background: #FDFC47;
    background: linear-gradient(to right, #24FE41, #FDFC47);
    color: #000000;
    text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
    padding: .5em 2.5em;
    transition: all .3s ease-in-out; }
    .section.section_one .enter_anchor .enter_btn:hover {
      opacity: .75; }

.section.section_two .section_two_title {
  margin: .5em auto 0;
  text-align: left;
  letter-spacing: .25em;
  padding: 0 1.5em; }

.section.section_three .section_title {
  margin: 0 auto;
  text-align: right;
  letter-spacing: .25em;
  padding: 0 1.5em; }

.fp-section {
  text-align: center; }

.gray {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
  -webkit-filter: gray;
          filter: gray;
  transition: all .25s ease-in-out; }
  .gray:hover {
    -webkit-filter: none;
    -moz-filter: none;
    -ms-filter: none;
    -o-filter: none;
    filter: none; }

